.marquee {
  cursor: -webkit-grab;
  display: -webkit-box;
}

.ant-popover .ant-popover-inner, .ant-popover .ant-popover-arrow:before {
  background-color: var(--cream);
}

.ant-carousel, .slick-slider, .slick-list, .slick-track, .slick-slide, .slick-slide * {
  /*height: 100%;*/
  max-height: 100%;
  overflow: hidden !important;
}

.slick-slide * > img {
  max-height: 100%;
  max-width: 100%;
}

